E,oBAAA,CAIJ,YACE,YAAA,CACA,cAAA,CACA,4BAAA,CACA,UAAA,CACA,eAAA,CAEA,mBACE,cAAA,CACA,WAAA,CACA,YAAA,CACA,qBAAA,CACA,+BAAA,CACA,mBAAA,CACA,iBAAA,CAEA,uBACE,iBAAA,CACA,WAAA,CACA,YAAA,CACA,kBAAA,CAGF,sBACE,gBAAA,CACA,mBAAA,CAGF,qBACE,cAAA,CACA,eAAA,CCjER,gBACE,wBAAA,CACA,aAAA,CAEA,wBACE,wBAAA,CACA,aAAA,CAEA,qCACE,aAAA,CAGF,gDACE,aAAA,CAEA,sDACE,abdQ,CamBd,wBACE,wBAAA,CACA,aAAA,CAEA,mDACE,aAAA,CAEA,yDACE,ab3BQ,CagCd,yBACE,wBAAA,CACA,aAAA,CAEA,yCACE,aAAA,CAGF,kDACE,aAAA,CAEA,wDACE,ab5CQ,CaiDd,sBACE,wBAAA,CACA,aAAA,CACA,qBAAA,CAEA,mCACE,wBAAA,CACA,aAAA,CAGF,iCACE,wBAAA,CACA,aAAA,CAGF,mCACE,wBAAA,CACA,aAAA,CCpEN,iBACE,wBAAA,CACA,UAAA,CAEA,yBACE,wBdHY,CcIZ,UAAA,CAEA,sCACE,UAAA,CAGF,iDACE,UAAA,CAEA,uDACE,aAAA,CAKN,yBACE,wBdbS,CccT,UAAA,CAEA,oDACE,UAAA,CAEA,0DACE,aAAA,CAKN,0BACE,wBdhCc,CciCd,UAAA,CAEA,0CACE,UAAA,CAGF,mDACE,UAAA,CAEA,yDACE,aAAA,CAKN,uBACE,qBAAA,CACA,UAAA,CACA,iCAAA,CAEA,oCACE,gCAAA,CACA,UAAA,CAGF,kCACE,qBAAA,CACA,UAAA,CAGF,oCACE,gCAAA,CACA,UAAA,CCrEN,KACE,4BAAA,CACA,QAAA,CACA,SAAA,CACA,YAAA,CACA,kBAAA,CACA,sBAAA,CACA,YAAA,CACA,wBAAA,CACA,UAAA,CAEA,gBACE,iBAAA,CAGF,QACE,aAAA,CACA,QAAA,CAGF,OACE,eAAA,CAGF,OACE,aAAA,CACA,oBAAA,CAEA,aACE,yBAAA,CC7BN,SACE,eAAA,CACA,wBAAA,CACA,wBAAA,CACA,iBAAA,CACA,YAAA,CCLF,cACE,wBAAA,CACA,UAAA,CAGF,yCACE,gCAAA,CAGF,WACE,gBAAA,CAGF,kBACE,WAAA,CACA,gBAAA,CAGF,gBACE,aAAA,CACA,gBAAA,CAGF,gBACE,SAAA,CACA,gBAAA","sourcesContent":["/* _reset.scss */\n\n#root,\n#__next { /* stylelint-disable-line selector-id-pattern */\n  isolation: isolate;\n}\n\n/* A modern CSS reset */\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n  margin: 0;\n  padding: 0;\n}\n\nhtml,\nbody {\n  height: 100%;\n  font-family: $font-family-base;\n}\n\nbody {\n  line-height: $line-height-base;\n  background-color: $light-color;\n  color: $dark-color;\n}\n\nimg,\npicture,\nvideo,\ncanvas {\n  display: block;\n  max-width: 100%;\n}\n\ninput,\nbutton,\ntextarea,\nselect {\n  font: inherit;\n}\n\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  overflow-wrap: break-word;\n}","/* _variables.scss */\n@use \"sass:math\";\n\n/* Color variables */\n$primary-color: #007bff;\n$secondary-color: #6c757d;\n$success-color: #28a745;\n$danger-color: #dc3545;\n$warning-color: #ffc107;\n$info-color: #17a2b8;\n$light-color: #f8f9fa;\n$dark-color: #343a40;\n\n/* Font variables */\n$font-family-base: 'Arial, sans-serif';\n$font-size-base: 16px;\n$line-height-base: 1.5;\n$font-weight-base: 400; /* Ensure this variable is defined */\n\n/* Spacing variables */\n$spacer: 1rem;\n$spacer-half: math.div($spacer, 2);\n$spacer-quarter: math.div($spacer, 4);\n\n/* Breakpoints */\n$breakpoint-sm: 576px;\n$breakpoint-md: 768px;\n$breakpoint-lg: 992px;\n$breakpoint-xl: 1200px;\n","/* _typography.scss */\n\nbody {\n  font-family: $font-family-base;\n  font-size: $font-size-base;\n  line-height: $line-height-base;\n  color: $dark-color;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  margin-bottom: 0.5rem;\n  font-weight: 500;\n  line-height: 1.2;\n  color: $dark-color;\n}\n\nh1 {\n  font-size: 2.5rem;\n}\n\nh2 {\n  font-size: 2rem;\n}\n\nh3 {\n  font-size: 1.75rem;\n}\n\nh4 {\n  font-size: 1.5rem;\n}\n\nh5 {\n  font-size: 1.25rem;\n}\n\nh6 {\n  font-size: 1rem;\n}\n\np {\n  margin-top: 0;\n  margin-bottom: 1rem;\n}\n\na {\n  color: $primary-color;\n  text-decoration: none;\n\n  &:hover {\n    text-decoration: underline;\n  }\n}\n","/* _base.scss */\n\nbody {\n  font-family: $font-family-base;\n  margin: 0;\n  padding: 0;\n  line-height: $line-height-base;\n  background-color: $light-color;\n  color: $dark-color;\n}\n\n.container {\n  width: 100%;\n  margin-right: auto;\n  margin-left: auto;\n  padding-right: $spacer;\n  padding-left: $spacer;\n\n  @include respond-to(sm) {\n    max-width: 540px;\n  }\n\n  @include respond-to(md) {\n    max-width: 720px;\n  }\n\n  @include respond-to(lg) {\n    max-width: 960px;\n  }\n\n  @include respond-to(xl) {\n    max-width: 1140px;\n  }\n}\n\nbutton {\n  cursor: pointer;\n}\n\n.hidden {\n  display: none !important;\n}\n\n.text-center {\n  text-align: center !important;\n}\n\n.text-right {\n  text-align: right !important;\n}\n\n.text-left {\n  text-align: left !important;\n}\n\n.mt-1 {\n  margin-top: $spacer-quarter !important;\n}\n\n.mt-2 {\n  margin-top: $spacer-half !important;\n}\n\n.mt-3 {\n  margin-top: $spacer !important;\n}\n\n.mb-1 {\n  margin-bottom: $spacer-quarter !important;\n}\n\n.mb-2 {\n  margin-bottom: $spacer-half !important;\n}\n\n.mb-3 {\n  margin-bottom: $spacer !important;\n}\n","/* _mixins.scss */\n\n/* Mixin for clearfix */\n@mixin clearfix() {\n  &::after {\n    content: \"\";\n    display: table;\n    clear: both;\n  }\n}\n\n/* Mixin for responsive font sizes */\n@mixin responsive-font($size) {\n  font-size: $size;\n\n  @media (max-width: $breakpoint-md) {\n    font-size: $size * 0.9;\n  }\n\n  @media (max-width: $breakpoint-sm) {\n    font-size: $size * 0.8;\n  }\n}\n\n/* Mixin for center alignment */\n@mixin center-align() {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n}\n\n/* Mixin for setting a gradient background */\n@mixin gradient-bg($start-color, $end-color) {\n  background: $start-color;\n  background: linear-gradient(45deg, $start-color, $end-color);\n}\n\n/* Mixin for media queries */\n@mixin respond-to($breakpoint) {\n  @if $breakpoint == sm {\n    @media (max-width: $breakpoint-sm) { @content; }\n  }\n\n @else if $breakpoint == md {\n    @media (max-width: $breakpoint-md) { @content; }\n  }\n\n @else if $breakpoint == lg {\n    @media (max-width: $breakpoint-lg) { @content; }\n  }\n\n @else if $breakpoint == xl {\n    @media (max-width: $breakpoint-xl) { @content; }\n  }\n}\n","/* _header.scss */\n\n.header {\n  background-color: $primary-color;\n  color: #fff;\n  padding: 1rem;\n\n  .header-logo {\n    font-size: 1.5rem;\n    font-weight: bold;\n  }\n\n  .header-nav {\n    display: flex;\n    justify-content: flex-end;\n    list-style: none;\n    padding: 0;\n    margin: 0;\n\n    .nav-item {\n      margin-left: 1rem;\n\n      a {\n        color: #fff;\n        text-decoration: none;\n\n        &:hover {\n          text-decoration: underline;\n        }\n      }\n    }\n  }\n}\n","/* _footer.scss */\n\n.footer {\n  background-color: $dark-color;\n  color: #fff;\n  padding: 1rem;\n  text-align: center;\n\n  .footer-links {\n    list-style: none;\n    padding: 0;\n    margin: 0;\n\n    .link-item {\n      display: inline-block;\n      margin: 0 0.5rem;\n\n      a {\n        color: #fff;\n        text-decoration: none;\n\n        &:hover {\n          text-decoration: underline;\n        }\n      }\n    }\n  }\n\n  .footer-copy {\n    margin-top: 1rem;\n    font-size: 0.875rem;\n  }\n}\n","/* _grid.scss */\n\n@use \"sass:math\";\n\n.container {\n  width: 100%;\n  margin-right: auto;\n  margin-left: auto;\n  padding-right: $spacer;\n  padding-left: $spacer;\n\n  @include respond-to(sm) {\n    max-width: 540px;\n  }\n\n  @include respond-to(md) {\n    max-width: 720px;\n  }\n\n  @include respond-to(lg) {\n    max-width: 960px;\n  }\n\n  @include respond-to(xl) {\n    max-width: 1140px;\n  }\n}\n\n.row {\n  display: flex;\n  flex-wrap: wrap;\n  margin-right: -$spacer;\n  margin-left: -$spacer;\n}\n\n.col {\n  position: relative;\n  width: 100%;\n  padding-right: $spacer;\n  padding-left: $spacer;\n}\n\n@for $i from 1 through 12 {\n  .col-#{$i} {\n    flex: 0 0 percentage(math.div($i, 12));\n    max-width: percentage(math.div($i, 12));\n  }\n}\n","/* _sidebar.scss */\n\n.sidebar {\n  background-color: $secondary-color;\n  color: #fff;\n  padding: 1rem;\n  width: 250px;\n  min-height: 100vh;\n\n  .sidebar-header {\n    font-size: 1.5rem;\n    font-weight: bold;\n    margin-bottom: 1rem;\n  }\n\n  .sidebar-nav {\n    list-style: none;\n    padding: 0;\n\n    .nav-item {\n      margin-bottom: 1rem;\n\n      a {\n        color: #fff;\n        text-decoration: none;\n\n        &:hover {\n          text-decoration: underline;\n        }\n      }\n    }\n  }\n}\n","/* _buttons.scss */\n\n/* Basic button styles */\n.btn {\n  display: inline-block;\n  font-weight: $font-weight-base;\n  text-align: center;\n  white-space: nowrap;\n  vertical-align: middle;\n  user-select: none;\n  border: 1px solid transparent;\n  padding: 0.375rem 0.75rem;\n  font-size: $font-size-base;\n  line-height: 1.5;\n  border-radius: 0.25rem;\n  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,\n    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;\n\n  &:hover {\n    text-decoration: none;\n  }\n\n  &-primary {\n    color: #fff;\n    background-color: $primary-color;\n    border-color: $primary-color;\n\n    &:hover {\n      color: #fff;\n      background-color: darken($primary-color, 10%);\n      border-color: darken($primary-color, 10%);\n    }\n  }\n\n  &-secondary {\n    color: #fff;\n    background-color: $secondary-color;\n    border-color: $secondary-color;\n\n    &:hover {\n      color: #fff;\n      background-color: darken($secondary-color, 10%);\n      border-color: darken($secondary-color, 10%);\n    }\n  }\n\n  &-success {\n    color: #fff;\n    background-color: $success-color;\n    border-color: $success-color;\n\n    &:hover {\n      color: #fff;\n      background-color: darken($success-color, 10%);\n      border-color: darken($success-color, 10%);\n    }\n  }\n\n  &-danger {\n    color: #fff;\n    background-color: $danger-color;\n    border-color: $danger-color;\n\n    &:hover {\n      color: #fff;\n      background-color: darken($danger-color, 10%);\n      border-color: darken($danger-color, 10%);\n    }\n  }\n\n  &-warning {\n    color: #212529;\n    background-color: $warning-color;\n    border-color: $warning-color;\n\n    &:hover {\n      color: #212529;\n      background-color: darken($warning-color, 10%);\n      border-color: darken($warning-color, 10%);\n    }\n  }\n\n  &-info {\n    color: #fff;\n    background-color: $info-color;\n    border-color: $info-color;\n\n    &:hover {\n      color: #fff;\n      background-color: darken($info-color, 10%);\n      border-color: darken($info-color, 10%);\n    }\n  }\n\n  &-light {\n    color: #212529;\n    background-color: $light-color;\n    border-color: $light-color;\n\n    &:hover {\n      color: #212529;\n      background-color: darken($light-color, 10%);\n      border-color: darken($light-color, 10%);\n    }\n  }\n\n  &-dark {\n    color: #fff;\n    background-color: $dark-color;\n    border-color: $dark-color;\n\n    &:hover {\n      color: #fff;\n      background-color: darken($dark-color, 10%);\n      border-color: darken($dark-color, 10%);\n    }\n  }\n}\n","/* _cards.scss */\n\n.card {\n  position: relative;\n  display: flex;\n  flex-direction: column;\n  min-width: 0;\n  word-wrap: break-word;\n  background-color: #fff;\n  background-clip: border-box;\n  border: 1px solid rgb(0 0 0 / 0.125);\n  border-radius: 0.25rem;\n\n  &-header {\n    padding: 0.75rem 1.25rem;\n    margin-bottom: 0;\n    background-color: rgb(0 0 0 / 0.03);\n    border-bottom: 1px solid rgb(0 0 0 / 0.125);\n  }\n\n  &-body {\n    flex: 1 1 auto;\n    padding: 1.25rem;\n  }\n\n  &-footer {\n    padding: 0.75rem 1.25rem;\n    background-color: rgb(0 0 0 / 0.03);\n    border-top: 1px solid rgb(0 0 0 / 0.125);\n  }\n\n  &-title {\n    margin-bottom: 0.75rem;\n  }\n\n  &-text {\n    margin-top: 0;\n    margin-bottom: 1rem;\n  }\n\n  &-img-top {\n    width: 100%;\n    border-top-left-radius: calc(0.25rem - 1px);\n    border-top-right-radius: calc(0.25rem - 1px);\n  }\n}\n","/* _modals.scss */\n\n.modal {\n  display: none;\n  position: fixed;\n  z-index: 1050;\n  inset: 0;\n  overflow: hidden;\n  outline: 0;\n\n  &-dialog {\n    position: relative;\n    width: auto;\n    margin: 0.5rem;\n    pointer-events: none;\n\n    @include respond-to(sm) {\n      margin: 1.75rem auto;\n      max-width: 500px;\n    }\n  }\n\n  &-content {\n    position: relative;\n    display: flex;\n    flex-direction: column;\n    width: 100%;\n    pointer-events: auto;\n    background-color: #fff;\n    backgr